@import url("https://fonts.googleapis.com/css2?family=Oswald&family=ZCOOL+QingKe+HuangYou&display=swap");

* {
  box-sizing: border-box;
}

html {
  background-color: #4e5e9f;
}

#root {
  margin: 8em auto auto auto;
  width: 330px;
}

#calculator {
  height: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  border-radius: 0.5em;
}

#brand {
  margin-top: 0;
  color: white;
  padding-left: 1em;
  padding-top: 1em;
  font-size: 1.2em;
  font-family: "Oswald", sans-serif;
  user-select: none
}

#screen {
  font-family: "ZCOOL QingKe HuangYou", cursive;
  background-color: #5dff00;
  border-style: inset;
  border-color: #10710c;
  margin: auto auto 2em auto;
  width: 90%;
  border-radius: 0.1em;
  user-select: none;
}

#display {
  font-size: 1.3em;
  width: 100%;
  height: 40px;
  word-wrap: break-word;
}

#tempDisplay {
  font-size: 1.5em;
  width: 100%;
  height: 30px;
  margin-top: 0;
}

#number,
#numberTemp {
  margin-top: 0;
  text-align: Right;
  padding: 0.2em;
}

#numberPad {
  margin: auto auto 2em auto;
  width: 90%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 1px;
  grid-row-gap: 1px;
}

button {
  width: 55px;
  height: 50px;
  background-color: white;
  border-radius: 0.1em;
  border-style: outset;
  border-color: white;
  border-width: 3px;
  font-family: "ZCOOL QingKe HuangYou", cursive;
  font-size: 1.3em;
  margin: 0.3em auto 0.3em auto;
  cursor:pointer;
  user-select: none;
}
button:active {
  color: White;
  border-color: black;
}

#historyRecall:active,
#clear:active,
#clearLast:active {
  color: #dd0606;
  border-color: black;
}

#add:active,
#subtract:active,
#multiply:active,
#divide:active {
  color: #55a6d1;
  border-color: black;
}
#equals:active {
  color: #d0a300;
  border-color: black;
}

#historyRecall,
#clear,
#clearLast {
  background-color: #dd0606;
  border-color: #dd0606;
  color: White;
}

#equals {
  width: 128px;
  grid-area: 5 / 3 / 6 / 5;
  background-color: #d0a300;
  color: White;
  border-color: #d0a300;
}

#add,
#subtract,
#multiply,
#divide {
  background-color: #55a6d1;
  color: White;
  border-color: #55a6d1;
}

#resultHistory {
  color: White;
  margin: auto;
  word-wrap: break-word;
  width: 90%;
}

ul {
  margin-top: 0;
  list-style-type: none;
  padding-left: 1em;
  padding-top: 0;
  max-height: 100px;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-family: "ZCOOL QingKe HuangYou", cursive;
  font-size: 1.3em;
}

::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 20px;
  border: transparent;
}

li {
  padding-top: 0.5em;
}

#add {
  grid-area: 4 / 4 / 5 / 5;
}
#subtract {
  grid-area: 3 / 4 / 4 / 5;
}
#multiply {
  grid-area: 2 / 4 / 3 / 5;
}
#divide {
  grid-area: 1 / 4 / 2 / 5;
}
#one {
  grid-area: 4 / 1 / 5 / 2;
}
#two {
  grid-area: 4 / 2 / 5 / 3;
}
#three {
  grid-area: 4 / 3 / 5 / 4;
}
#four {
  grid-area: 3 / 1 / 4 / 2;
}
#five {
  grid-area: 3 / 2 / 4 / 3;
}
#six {
  grid-area: 3 / 3 / 4 / 4;
}
#seven {
  grid-area: 2 / 1 / 3 / 2;
}
#eight {
  grid-area: 2 / 2 / 3 / 3;
}
#nine {
  grid-area: 2 / 3 / 3 / 4;
}
#zero {
  grid-area: 5 / 1 / 6 / 2;
}
#decimal {
  grid-area: 5 / 2 / 6 / 3;
}


::selection {
  color: black;
  background: white;
}

@media only screen and (max-width: 430px) {
  #root{
    margin: 1em auto auto auto;
  }
}

@media only screen and (max-width: 340px) {
  #root{
   width: 300px;
  }
}